@use "../util" as *;

:root {
  --card-width: 250px;
  --card-border-radius: 16px;
  --row-increment: 10px;
  --card-small: 26;
  --card-medium: 33;
  --card-large: 45;
}

.container {
  padding: 0;
  max-width: rem(1100);
  margin: 0 auto;
}

.grid {
  display: grid;
  gap: 1.5rem;
}
