@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");
.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.section {
  padding: 2rem 0 4rem;
  height: 100vh;
}

.section__title {
  font-size: var(--h1-font-size);
}

.section__subtitle {
  display: block;
  font-size: var(--small-font-size);
  margin-bottom: var(--mb-3);
}

.section__title,
.section__subtitle {
  text-align: center;
}

.card {
  padding: 0;
  margin: 0.9375rem 0.625rem;
  border: 1px solid;
  border-radius: var(--card-border-radius);
  background-color: transparent;
}
.card--small {
  grid-row-end: span var(--card-small);
}
.card--medium {
  grid-row-end: span var(--card-medium);
}
.card--large {
  grid-row-end: span var(--card-large);
}
:root {
  --card-width: 250px;
  --card-border-radius: 16px;
  --row-increment: 10px;
  --card-small: 26;
  --card-medium: 33;
  --card-large: 45;
}

.pin__container {
  margin: 0;
  padding: 0;
  background-color: white;
  width: 80vw;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fill, var(--card-width));
  grid-auto-rows: var(--row-increment);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font: inherit;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  width: 100vw;
  height: 100vh;
}

:root {
  --hue-color: 215;
  --saturation: 82%;
  --lightness: 99%;
  --primary-clr-300: hsl(215, 82%, 61%);
  --primary-clr-400: hsl(215, 82%, 51%);
  --primary-clr-500: hsl(215, 82%, 41%);
  --primary-clr-600: hsl(215, 82%, 31%);
  --secondary-clr-300: hsl(215, 57%, 53%);
  --first-color-lighter: hsl(215, 92%, 85%);
  --title-color: hsl(215, 8%, 15%);
  --text-color: hsl(215, 8%, 45%);
  --text-color-light: hsl(215, 8%, 65%);
  --input-color: hsl(215, 70%, 96%);
  --input-color-darken: hsl(215, 70%, 90%);
  --body-color: hsl(215, 10%, 98%);
  --header-color: hsl(215, 67%, 95%);
  --container-color: #fff;
  --scroll-bar-color: hsl(215, 12%, 90%);
  --scroll-thumb-color: hsl(215, 37%, 31%);
}

body.dark-theme {
  --first-color-second: hsl(var(--hue-color), 30%, 8%);
  --title-color: hsl(var(--hue-color), 8%, 95%);
  --text-color: hsl(var(--hue-color), 8%, 75%);
  --input-color: hsl(var(--hue-color), 29%, 16%);
  --body-color: hsl(var(--hue-color), 28%, 12%);
  --header-color: hsl(var(--hue-color), 67%, 7%);
  --container-color: hsl(var(--hue-color), 29%, 16%);
  --scroll-bar-color: hsl(var(--hue-color), 12%, 90%);
  --scroll-thumb-color: hsl(var(--hue-color), 37%, 31%);
}

:root {
  --body-font: "Poppins", sans-serif;
}

:root {
  --card-width: 250px;
  --card-border-radius: 16px;
  --row-increment: 10px;
  --card-small: 26;
  --card-medium: 33;
  --card-large: 45;
}

.container {
  padding: 0;
  max-width: 68.75rem;
  margin: 0 auto;
}

.grid {
  display: grid;
  gap: 1.5rem;
}

:root {
  --header-height: 3rem;
  --xs-font-size: 0.75rem;
  --s-font-size: 0.813rem;
  --m-font-size: 0.938rem;
  --l-font-size: 2rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1.125rem;
  --font-medium: 500;
  --font-semi-bold: 600;
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;
  --z-tolltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}
@media (min-width: 71.875em) {
  :root {
    --xs-font-size: 0.813rem;
    --s-font-size: 0.875rem;
    --m-font-size: 1rem;
    --l-font-size: 3rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: inherit;
}

ul,
li {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  box-shadow: 0;
  border: 0;
  font-family: inherit;
  /* 1 */
  font-size: 100%;
  /* 1 */
  line-height: 1;
  /* 1 */
  margin: 0;
  /* 2 */
}

textarea {
  resize: none;
}